.clockout {
    color: rgb(249, 249, 239);
    font-size: 16px;
    font-weight: 700;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    position: absolute;
    top: 25%;
    left: 65%;
    border: none;
    background-color: #4682b4;
  }
  .clockin {
    color: rgb(249, 249, 239);
    font-size: 16px;
    font-weight: 700;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    position: absolute;
    top: 25%;
    left: 15%;
    border: none;
    background-color: #4682b4;
  }
  .punch-button {
    position: absolute;
    top: 15%;
    left: 35%;
    right: 35%;
  }
  .status {
    position: absolute;
    bottom: 50%;
    left: 5%;
    text-align: center;
    font-weight: 600;

  }