body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #b9deec;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  /* margin: 2em 1em 2em 1em;
  padding-bottom: 1em; */
  border-bottom: 1px solid;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 62px;
  background-color: #4682b4;
  font-size:large;

}
.header a {
  margin: 0 1em 0 1em;
  font-size: 1.2em;
  font-weight: 600;
  color:rgb(249, 249, 239);
  text-decoration: none;
  padding: 10px 26px 0 30px;
}

.content {
  margin-left: 2em;
  font-size: 1.3em;
}

.message {
  color: red;
  font-size: 0.7em;
}
input[type="text"],
input[type="password"]{
  width: 85%;
  padding: 9px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 20px;
  background-color: rgb(252, 237, 248);
}
input[type="submit"], input[type="button"], .punch-button {
  padding: 10px 20px;
  color:rgb(249, 249, 239);
  font-size: large;
  font-weight: 600;
  position: absolute;
  right: 10%;
  border-radius: 12px;
  border: 1px solid;
  background-color: #4682b4;
}
.account-layout {
  font-weight: 500;
  padding: 10px 0 0 20px;
}

.login-layout, .register-layout{
  padding: 0 0 0 10%;
}